body{
    font-family: "Karla", sans-serif;
    margin: 0;
}
.navbar{
    width: 100%;
    color: white;
    background-image: linear-gradient(to right,rgb(182, 182, 112),rgb(188, 188, 158));
    display: flex;
    align-items: center;
    padding: 20px;
}
.logo-img{
    width: 80px;
    height: 80px;
    margin-right: 10px;
}
.HeaderText{
    margin-right: auto;
}
.Learn-React{
    margin-right: 50px;
    color: red;
}
/* --------------- Meme generator form -------------------------*/
.meme-form{
    margin-top: 50px;
    display: grid;
    grid-template: 40px 40px / 1fr 1fr;
    gap: 10px;
}
.form-button{
    grid-column: 1/-1;
    width: 300px;
    align-items: center;
    margin: auto;
    height: 80px;
    background-image: linear-gradient(to right,rgb(182, 182, 112),rgb(188, 188, 158));
}
.text{
    margin: auto;
    width: 300px;
    height: 50px;
    padding:0.5rem;
    font-size: 20px;
}
/* -----------Image -------------------------------- */
.meme-div{
width: 500px;
height: 500px;
margin-top: 100px;
margin-left: 40%;
position: relative;
}
.meme-image{
    max-width: 100%;
   max-height: 100%
}
.meme--text {
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 15px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}

#bottom {
    bottom: 0;
}

#top {
    top: 0;
}
